.u-pad-top {
  padding-top: 16px;
  padding-top: 1rem; }

.u-pad-top-lg {
  padding-top: 32px;
  padding-top: 2rem; }

.u-pad-bottom {
  padding-bottom: 16px;
  padding-bottom: 1rem; }

.u-pad-bottom-lg {
  padding-bottom: 32px;
  padding-bottom: 2rem; }

/*------------------------------------*    $VARIABLES
\*------------------------------------*/
