  list-style: none;
  /* [1] */
  margin: 0;
  /* [2] */
  padding: 0;
  /* [2] */
  margin-left: -1.6%;
  /* [3] */
  margin-right: -1.6%;
  zoom: 1;
  /* ie 6/7 */ }
  .grid:before, .grid:after {
    content: "";
    display: table; }
  .grid:after {
    clear: both; }

/**
 * 1. Space columns apart.
 * 2. Required to combine fluid widths and fixed gutters.
 */
